"use client";

import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";
import { getStrapiData } from "@/lib/strapi";
import { ChevronDown } from "lucide-react";
import Link from "next/link";
import { useEffect, useState } from "react";

type NavbarItem = {
  id: number;
  title: string;
  slug: string;
  order: number;
  parent: {
    id: number;
    title: string;
    slug: string;
  } | null;
};

export default function Navbar() {
  const [menus, setMenus] = useState<NavbarItem[]>([]);

  useEffect(() => {
    async function fetchData() {
      try {
        // ✅ Ambil data dari Strapi (populate relasi parent)
        const res = await getStrapiData<NavbarItem>("/menus?populate=parent");

        // ✅ Pastikan format array
        const items = Array.isArray(res.data) ? res.data : [];

        // ✅ Sort aman
        const sorted = items.sort((a, b) => (a?.order ?? 0) - (b?.order ?? 0));

        setMenus(sorted);
      } catch (error) {
        console.error("Error fetching menus:", error);
      }
    }

    fetchData();
  }, []);

  // ✅ Parent & child
  const parents = menus.filter((m) => !m.parent);
  const children = menus.filter((m) => m.parent);

  return (
    <nav className="border-b border-gray-200">
      <div className="max-w-7xl mx-auto px-6 flex justify-between items-center h-16">
        <Link href="/" className="font-bold text-lg tracking-wide uppercase">
          Elzatta
        </Link>

        <ul className="flex space-x-8 text-sm font-medium text-gray-800">
          {parents.map((parent) => {
            const submenus = children.filter((c) => c.parent?.id === parent.id);

            if (submenus.length > 0) {
              return (
                <li key={parent.id} className="relative">
                  <DropdownMenu>
                    <DropdownMenuTrigger className="flex items-center gap-1 hover:border-b-2 border-black transition">
                      {parent.title}
                      <ChevronDown className="h-4 w-4" />
                    </DropdownMenuTrigger>
                    <DropdownMenuContent className="rounded-none border border-gray-200">
                      {submenus.map((sub) => (
                        <DropdownMenuItem key={sub.id} asChild>
                          <Link href={sub.slug} className="w-full">
                            {sub.title}
                          </Link>
                        </DropdownMenuItem>
                      ))}
                    </DropdownMenuContent>
                  </DropdownMenu>
                </li>
              );
            }

            return (
              <li key={parent.id}>
                <Link
                  href={parent.slug}
                  className="py-2 hover:border-b-2 border-black transition"
                >
                  {parent.title}
                </Link>
              </li>
            );
          })}
        </ul>
      </div>
    </nav>
  );
}
